<template>
  <div class='main'>
      <div class="options">

            <input id='itemName' type="text" name="item name"/>
            <input @click='addItem' type="submit" value="Add"/>
          

        </div>
        <div class="items">
            <ul>
                <div v-bind:key="item.id" v-for="item in items">
                <h5>{{item.name }}
                    <button class='cmp' @click='tagComplete(item.id)'>
                        Tag complete
                    </button>
                    <button class='btn' @click='removeItem(item.id)'>
                        Remove
                    </button>
                </h5>
                    <div v-if="item.complete == true">
                        <p>completed!</p>
                    </div>
                
                
                </div>
            
            </ul>

      </div>
  </div>
</template>

<script>
export default {
    name: 'ToDoList',
    data(){
        return{
            idName:2,
            items:[
                {
                    id:1,
                    name: 'bruno',
                    complete: true
                }
            ]
        }
    }
    ,
    methods:{
        addItem(){
            this.idName=this.idName+1;
            let newName=document.getElementById('itemName').value;
            let newItem= {
                    id:this.idName,
                    name: newName,
                    complete: false
                }
            this.items.push(newItem);
            
        },
        removeItem(id){
            this.items = this.items.filter(item => item.id !== id);

        },
        tagComplete(id){
            this.items.map(item => {
                if (item.id == id){
                    item.complete = !item.complete;
                }
            })
        }
    }

}
</script>

<style scoped>

.btn {
    float: right ;
}


</style>